.featured-image-preview {
  position: relative;
  display: flex;
  width: 100%;
  border-radius: $tip-border-radius;
  height: $summary-img-height;
  .mycontent,img {
    width: 100%;
    height: 100%;
    border-top-left-radius: $tip-border-radius;
    border-top-right-radius: $tip-border-radius;
    clip-path: polygon(0 130px,0 0,100% 0,100% 130px,50% 100%);
  }

  .mycontent {
    background-color: rgba($color: #000, $alpha: 0.4);
    z-index: 1;
    &:hover{
      font-size: 19px;
      transition: font-size 0.5s;
    }
    width: 100%;
    height: 100%;
    position:absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    bottom: 0;
  }
}

.post-footer {
  margin: 0 0;
  padding: 0 1em;

  white-space: nowrap;
  font-size: 14px;

  .author {
    display: flex;
    align-items: center;
  }

  a {
    color: $global-font-color !important;

    [data-theme='dark'] & {
      color: $global-font-color-dark !important;
    }
  }

  &:has(~.post-reward) {
    margin-bottom: 0;
  }

  .post-info {
    border-bottom: 1px solid $global-border-color;
    padding: 1rem 0 0.3rem;

    [data-theme='dark'] & {
      border-bottom: 1px solid $global-border-color-dark;
    }

    .post-info-line {
      display: flex;
      justify-content: space-between;

      .post-info-mod {
        font-size: 0.8em;
        color: $global-font-secondary-color;

        [data-theme='dark'] & {
          color: $global-font-secondary-color-dark;
        }

        @include link(false, false);
      }

      .post-info-license {
        font-size: 0.8em;
        color: $global-font-secondary-color;

        [data-theme='dark'] & {
          color: $global-font-secondary-color-dark;
        }

        @include link(false, false);
      }

      .post-info-md {
        font-size: 0.8rem;
        width: fit-content;

        @include link(false, false);

        >span:not(:first-child)::before {
          content: '|';
          margin-left: 0.25rem;
          margin-right: 0.25rem;
        }
      }

      .post-info-share {
        a * {
          vertical-align: text-bottom;
        }
      }
    }
  }

  .post-info-more {
    padding: 0.3rem 0 1rem;
    display: flex;
    justify-content: space-between;
    font-size: 0.9rem;
  }

  .post-tags {
    max-width: 65%;

    * {
      display: inline;
    }

    @include link(true, true);

    .post-tag:not(:last-child)::after {
      content: ',';
      margin-right: 0.25rem;
    }
  }

  .post-nav {
    display: flex;
    justify-content: space-between;
    gap: 1.5rem;

    .post-nav-item {
      flex: 1;
      font-size: 1rem;
      font-weight: 600;
      @include transition(all 0.3s ease-out);

      &[rel='next'] {
        text-align: right;
      }

      &[rel='prev']:hover {
        @include transform(translateX(-4px));
      }

      &[rel='next']:hover {
        @include transform(translateX(4px));
      }
    }
  }
}